import { createStore } from 'vuex'
// import axios from 'axios'

const store = createStore({
  state(){
    return {
      likes : 30,
      좋아요눌렀니 : false,
      more : {},
    }
  },
  mutations : {
      좋아요(state){
          if(state.좋아요눌렀니 == false) {
            state.likes++;
            state.좋아요눌렀니 = true;
          }else{
              state.likes--
              state.좋아요눌렀니 = false
          }
      }
  },
  actions : {
  }
})

export default store