import { createApp } from 'vue'
import App from './App.vue'
import mitt from 'mitt'     // ./로 시작안하면 내가 설치한 라이브러리 임포트하는 문법
let emitter = mitt();       // emitter를 모든 컴포넌트가 이용하게 하고 싶으면
let app = createApp(App)    // app이 뷰 설정 다루는 부분
app.config.globalProperties.emitter = emitter;      //글로벌한 변수보관함임 모든컴포넌트들이 다 가져다 쓸수 있는 변수들 사용하고 싶을때 여기다가 추가

import store from './store.js'

app.use(store).mount('#app')
